<template>
  <div id="xg-loadding">
    <div :class="{ani:ani==0}"  style="color: #FFCC99;">X</div>
    <div :class="{ani:ani==1}" style="color: #CCFFFF;">g</div>
    <div :class="{ani:ani==2}" style="color: #FFFFCC;">u</div>
    <div :class="{ani:ani==3}" style="color: #FFFFCC;">n</div>
    <div :class="{ani:ani==4}">。</div>
    <div :class="{ani:ani==5}">。</div>
    <div :class="{ani:ani==6}">。</div>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        ani:0
      }
    },
    computed:{
      path(){
        return this.$route.query.path
      }
    },
    mounted() {
      var timer = setInterval(()=>{
        this.ani++;
        this.ani%= 7;
      },500)

      setTimeout(()=>{
        this.$router.push({path:this.path,query:{LoadComplete:true}})
      },1000)
    }
  }
</script>

<style scoped>
  @keyframes ani{
    0% {
      padding-bottom: 0em;
    }
    25% {
      padding-bottom: 1em;
    }
    100% {
      padding-bottom: 0em;
    }
  }
  #xg-loadding{
    width: 100%;
    height: 100vh;
    font-size: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .ani{
    animation: ani .5s ease;
  }
</style>



